<template>
    <div class="orderDeta_zb">
        <div class="pagetitle">
            <div>
                <router-link to="/">
                    <van-icon name="arrow-left" size="14px"></van-icon>
                    <span style="margin-left: 10px"><b></b></span>
                </router-link>
            </div>
        </div>
        <table class="order_table" >
            <tr>
                <th></th>
                <th></th>
            </tr>
            <tr>
                <th></th>
                <th></th>
            </tr>
            <tr >
                <td >{{orderdata.order_num}}</td>
                <td v-if="orderdata.order_type == '1'"></td>
                <td v-if="orderdata.order_type == '2'"></td>
                <td v-if="orderdata.order_type == '3'"></td>
                <td v-if="orderdata.order_type == '4'"></td>
            </tr>
            <tr>
                <td >{{orderdata.other_account}}</td>
                <td >NT$ {{orderdata.order_twd}}</td>
            </tr>

        </table>
        <table class="order_table" >
            <tr>
                <th></th>
                <th>
                    <span v-if="orderdata.payment_method==1 || orderdata.payment_method==6 || orderdata.payment_method==4 || orderdata.payment_method==9"></span>
                    <span v-if="orderdata.payment_method==2"></span>
                    <span v-if="orderdata.payment_method==3"></span>
                    <span v-if="orderdata.payment_method==5"> + </span>
                </th>

            </tr>
            <tr>
                <th></th>
                <th></th>
            </tr>
            <tr >
                <td>
                    <span v-if="orderdata.payment_method==9">(822) </span>
                    <span v-if="orderdata.payment_method==8">(810) </span>
                    <span v-if="orderdata.payment_method==2"></span>
                    <span v-if="orderdata.payment_method==3"></span>
                    <span v-if="orderdata.payment_method==5"> + </span>
                </td>
                <td>
                    <span v-if="orderdata.payment_method==9 || orderdata.payment_method==8">{{orderdata.payment_account}}</span>
                    <span  v-if="orderdata.payment_method==2"> : {{orderdata.payment_account}}</span>
                </td>
            </tr>
            <tr>
                <td >{{orderdata.create_time | convTimehms}}</td>
                <td>
                    <span  v-if="orderdata.order_state==3" style="color:#FFA801"></span>
                    <span  v-if="orderdata.order_state==4" style="color:#004bd9"></span>
                    <span  v-if="orderdata.order_state==6" style="color:#49ff00"></span>
                    <span  v-if="orderdata.order_state==7" style="color:#ff5854"></span>
                    <span  v-if="orderdata.order_state==10" style="color:#ff0017"></span>
                </td>
            </tr>
        </table>
        <div style="padding: 10px">
            <van-button type="danger" block color="#e01010" @click="cancel_platorder"></van-button>
        </div>
    </div>
</template>

<script>
    export default {
        data: function () {
            return {
                orderdata:{}
            }
        },
        components: {},
        methods: {
            getorderdata_zb(id){
                this.$http.post('https://www.trillionricher.com/1.0/GetPlat/',{
                    merber_id: this.OS.merber_id,
                    token:this.OS.token,
                    pid:id
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success==-1){
                        this.Logout()
                    }
                    if(res.body.success==1){
                        this.orderdata =  res.body.data
                    }
                },function(res){
                    console.log(res);
                });
            },
            cancel_platorder(){
                this.$dialog.alert({
                    message: '?',
                    showCancelButton:true,
                    confirmButtonText:'',
                }).then(() => {
                    this.$http.post('https://www.trillionricher.com/1.0/cancel_platorder/',{
                        merber_id: this.OS.merber_id,
                        token:this.OS.token,
                        pid:this.orderdata.pid
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success===1){
                            this.$toast({
                                message:'',
                                type:'success',
                                duration:1500,
                            });
                            this.getorderdata_zb(this.orderdata.pid)
                        }else {

                        }
                    },function(res){
                        console.log(res);
                    });
                }).catch(() => {
                    // on cancel
                });
            }
        },
        computed: {},
        watch: {},
        mounted: function () {
            var id=this.$route.query.id
            this.getorderdata_zb(id)
        }
    }
</script>
