<script>
    export default {
        data: function () {
            return {
                orderdata:'',
                show:false,
                merber_id:Cookies.get('merber_id'),
                refund_reason:'',
                columns: ['', '', '', '']
            }
        },
        components: {},
        methods: {
            Transformation:function(line){
                line=line-1
                var s=parseInt((line*10+120))
                var h=Math.floor(s/60);
                if(s>60){
                    return h+''+(s%60)+''
                }else {
                    return s+'';
                }
            },
            gosubStitute:function(){
                this.$router.push({
                    path:'/daifu',
                    query:{
                        Unfulfilled_order:true
                    }
                });
            },
            getorderDetails:function () {
                var order_id=this.$route.query.id
                this.$http.post('https://www.trillionricher.com/1.0/order_details',{
                    order_id:order_id,
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success===1){
                        this.orderdata=res.body.data[0]
                    }
                },function(res){
                    console.log(res);
                });
            },
            onChange:function (picker, value, index) {
                this.refund_reason=value
            },
            refundOrder:function () {
                var order_id=this.$route.query.id
                this.$http.post('https://www.trillionricher.com/1.0/refund_order',{
                    merber_id:this.merber_id,
                    token:this.OS.token,
                    order_id:order_id,
                    refund_reason:this.refund_reason,
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success==-1){
                        this.Logout()
                    }else if(res.body.success===1){
                        this.$toast({
                            message:'E',
                            type:'success',
                            duration:1500,
                        });
                    }else if(res.body.success===2){
                        this.$toast({
                            message:'!',
                            type:'success',
                            duration:1500,
                        });
                    }else {
                        this.$toast({
                            message:'',
                            type:'fail',
                            duration:1500,
                        });
                    }
                },function(res){
                    console.log(res);
                });
            },
            cancel_order:function () {
                this.$dialog.alert({
                    message: '',
                    showCancelButton:true,
                    confirmButtonText:'',
                }).then(() => {
                    var order_id=this.$route.query.id
                    this.$http.post('https://www.trillionricher.com/1.0/cancel_order',{
                        order_id:order_id,
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success===1){
                            this.$toast({
                                message:'',
                                type:'success',
                                duration:1500,
                            });
                            this.$router.push({path:'/'})
                        }else {
                            this.$toast({
                                message:'!',
                                type:'fail',
                                duration:1500,
                            });
                        }
                    },function(res){
                        console.log(res);
                    });
                }).catch(() => {
                    // on cancel
                });
            },
            uporderdata:function(){
                var order_id=this.$route.query.id
                this.$http.post('https://www.trillionricher.com/1.0/order_details',{
                    order_id:order_id,
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success===1){
                        this.orderdata=res.body.data[0]
                    }
                },function(res){
                    console.log(res);
                });
            },
        },
        computed: {},
        watch: {},
        mounted: function () {
            var _this=this
            if(this.merber_id==undefined){
                this.$router.push('/login');
            }else {
                this.getorderDetails()
                window.setInterval(() => {
                    setTimeout(_this.uporderdata(),0)
                }, 30000)
            }
        }
    }
</script>
