            },
            //
            onAgain() {
                console.log('');
                this.msg = 'try again';
                // 
                this.$refs.slideblock.reset();
            },

            phonetimer() {
                this.yzm.disabled=true
                if (this.yzm.time > 0) {
                    this.yzm.time--;
                    this.yzm.text=this.yzm.time+"s";
                    setTimeout(this.phonetimer, 1000);
                } else{
                    this.yzm.time=0;
                    this.yzm.text="";
                    this.yzm.disabled=false;
                }
            },

            //
            Transformation:function(line){
                line=line-1
                var s=parseInt((line*10+120))
                var h=Math.floor(s/60);
                if(s>60){
                    return h+''+(s%60)+''
                }else {
                    return s+'';
                }
            },
            getexchange:function(){
                this.$http.post('https://www.trillionricher.com/1.0/exchange',{
                    exchange_type:1,
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success===1){
                        this.exchange=res.body.data[0].exchange
                    }
                },function(res){
                    console.log(res);
                });
            },
            timer:function () {
                var _this=this
                var Toast=this.$toast
                const toast = Toast.loading({
                    duration: 0,       //  toast
                    forbidClick: true, // 
                    loadingType: 'spinner',
                    message: ' 60 '
                });
                if(this.second>0){
                    this.second--;
                    toast.message = ` ${this.second} `;
                    this.$http.post('https://www.trillionricher.com/1.0/product_order',{
                        order_id:this.order_id
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success===0){
                            if(this.second>0){
                                setTimeout(this.timer, 1000);
                            }else {
                                if(this.$route.path=='/zkl'){
                                    toast.clear();
                                    this.$dialog.alert({
                                        message: '<span style="color: red;font-weight: bold">'+res.body.data.line+'</span> <span style="color: red;font-weight: bold">'+this.Transformation(res.body.data.line)+'</span>',
                                        showCancelButton:true,
                                        confirmButtonText:'',
                                        cancelButtonText: '',
                                    }).then(() => {
                                        _this.$router.push({
                                            path:'/orderdata',
                                            query:{
                                                id:res.body.data.order_id
                                            }
                                        });
                                    }).catch(() => {
                                        _this.second=60
                                        setTimeout(this.timer, 1000);
                                    });
                                }
                            }

                        }else if(res.body.success===2){
                            toast.clear()
                            this.$dialog.alert({
                                message: ''
                            }).then(()=>{
                                    location.reload()
                                }
                            );
                        }else if(res.body.success===3){
