                            var mailReg = /^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)/g
                            if(this.PersonalEmail=='' || !mailReg.test(this.PersonalEmail)){
                                this.PersonalEmailerror_message=''
                            }else {
                                this.merber_email = this.PersonalEmail
                                this.company_add=''//
                                this.company_num=''//
                                this.LoveCode=''//
                                this.CarruerNum=''//
                                this.createorder_ajax()
                            }
                        }else if(this.invoice_type==2){
                            var codeReg=/^[0-9]{3,7}/
                            if(this.LoveCode==''){
                                var code=this.donation_num[Math.round(Math.random()*(this.donation_num.length-1))]
                                this.LoveCode=code.num
                                this.createorder_ajax()
                            }else if(!codeReg.test(this.LoveCode)){
                                this.LoveCodeerror_message=''
                            }else {
                                this.merber_email = Cookies.get('merber_email')
                                this.company_add=''//
                                this.company_num=''//
                                this.CarruerNum=''//
                                this.createorder_ajax()
                            }
                        }else if(this.invoice_type==3){
                            var mailReg = /^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)/g
                            if(!mailReg.test(this.companyEmail)){
                                this.companyEmailerror_message=''
                            }else if(!this.isValidGUI(this.company_num)){
                                this.company_numerror_message=''
                            }else if( this.company_add==''){
                                this.company_adderror_message=''
                            }else if(this.company_name == ''){
                                this.company_nameerror_message=''
                            }else{
                                this.merber_email=this.companyEmail
                                this.LoveCode=''//
                                this.CarruerNum=''//
                                this.createorder_ajax()
                            }
                        }else if(this.invoice_type==4){
                            var carReg=/^[A-Z]{2}[0-9]{14}$/
                            var car_tReg=/^\/{1}[0-9A-Z]{7}$/
                            var car2_Reg=/^\/[A-Z0-9\+\-\.]{7}$/
                            if(!carReg.test(this.CarruerNum) && !car_tReg.test(this.CarruerNum) && !car2_Reg.test(this.CarruerNum)){
                                this.CarruerNumerror_message='()'
                            }else {
                                this.merber_email = Cookies.get('merber_email')
                                this.company_add=''//
                                this.company_num=''//
                                this.LoveCode=''//
                                this.createorder_ajax()
                            }
                        }else {
                            this.createorder_ajax()
                        }
                    }
                }else {
                    this.$router.push('/')
                }
            },
            //
            createorder_ajax:function(){
                var _this=this
                var twd=this.OrderData.robot_rmb*this.exchange
                if(this.payment_method==2 && twd>5970){
                    this.$toast({
                        message:'5970',
                        type:'fail',
                        duration:3000,
                    });
                }else if(this.payment_method==3 ){
                    if(Number(this.OrderData.r_money) <=0 ){
                        this.$toast({
                            message:'E,',
                            type:'fail',
                            duration:3000,
                        });
                    }else {
                        this.dragverify = true
                    }

                }else {
                    this.nextbeld=true
                    //-----------
                    var timestamp = Date.parse(new Date());
                    var num = '';
                    for(var i=0;i<5;i++)
                    {
                        num+=Math.floor(Math.random()*10);
                    }
                    var sign = timestamp + num
                    //------------
                    this.$http.post('https://www.trillionricher.com/1.0/create_order',{
                        sign:sign,
                        order_id:this.order_id,
                        merber_id:this.merber_id,
                        token:this.OS.token,
                        is_mobile:1,
                        payment_method:this.payment_method,
                        alipay_type:this.alipay_type,
                        service_charge:'',
                        remarks:'',
                        platform_serve:this.platform_serve,//
                        invoice_type:this.invoice_type,//
                        CustomerEmail:this.merber_email,
                        CustomerAddr:this.company_add,//
                        CustomerIdentifier:this.company_num,//
                        LoveCode:this.LoveCode,//
                        CustomerName:this.invoice_type==3?this.company_name:this.merber_name,//
                        CarruerNum:this.CarruerNum,//
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success==-1){
                            this.Logout()
                        }else if(res.body.success===1){
                            if(this.payment_method==2){
                                this.loding=true
                                this.chaoshang=res.body.data
                                window.setTimeout(function () {
                                    document.getElementById("__ecpayForm").submit();
                                },100)
                            }else{
                                if(res.body.data.payment_method==1){
                                    _this.$toast({
                                        message:'',
                                        type:'success',
                                        duration:3000,
                                    });
                                }else if(res.body.data.payment_method==3){
                                    _this.$toast({
                                        message:'',
                                        type:'success',
                                        duration:3000,
                                    });
                                }
                                this.nextText=''
                                this.createorderdata=res.body.data
                                this.active=2
                                window.setTimeout(function () {
                                    _this.$router.push({
                                        path:'/orderdata',
                                        query:{
                                            id:res.body.data.order_id
                                        }
                                    });
                                },10000)
                            }
                        }else if(res.body.success==4){
                            this.$dialog.alert({
                                message: 'E,',
                                showCancelButton:true,
                                confirmButtonText:'',
                                cancelButtonText: '',
                            }).then(() => {
                                _this.$router.push({ path:'/rbcz'})
                            }).catch(() => {
                                // on cancel
                            });
                        }else if(res.body.success==11){
                            this.$toast({
                                message:'',
                                type:'fail',
                                duration:3000,
                            });
                            _this.$router.push({ path:'/order_list'})
                        }
                    },function(res){

                    });
                }
            },
            //E
            EBcreateorderajax:function(){
                var _this=this
                this.nextbeld=true

                if(this.phone_code == ""){
                    this.$toast({
                        message:'',
                        type:'fail',
                        duration:3000,
                    });
                }else {

                    if(Number(this.OrderData.r_money) >= Number(this.OrderData.robot_rmb)){
                        //-----------
                        var timestamp = Date.parse(new Date());
                        var num = '';
                        for(var i=0;i<5;i++)
                        {
                            num+=Math.floor(Math.random()*10);
                        }
                        var sign = timestamp + num
                        //------------
                        this.$http.post('https://www.trillionricher.com/1.0/create_order',{
                            sign:sign,
                            order_id:this.order_id,
                            merber_id:this.merber_id,
                            token:this.OS.token,
                            is_mobile:1,
                            payment_method:this.payment_method,
                            alipay_type:this.alipay_type,
                            service_charge:'',
                            remarks:'',
                            withhold_type:this.withhold_type,
                            payment_code:this.phone_code,
                            invoice_type:this.invoice_type,//
                            CustomerEmail:this.merber_email,
                            platform_serve:0,//

                            merber_phone:this.merber_phone,
                            CustomerAddr:this.company_add,//
                            CustomerIdentifier:this.company_num,//
                            LoveCode:this.LoveCode,//
                            CustomerName:this.invoice_type==3?this.company_name:this.merber_name,//
                            CarruerNum:this.CarruerNum,//
                        },{emulateJSON:true}).then(function(res){
                            this.loding=false
                            this.EBsubdaifubled = false
                            if(res.body.success==-1){
                                this.Logout()
                            }else if(res.body.success===1){
                                this.Supplement=false
                                if(this.withhold_type==2){
                                    this.loding=true
                                    this.chaoshang=res.body.data
                                    window.setTimeout(function () {
                                        document.getElementById("__ecpayForm").submit();
                                    },100)
                                }else{
                                    if(res.body.data.payment_method==1 || res.body.data.payment_method==5){
                                        this.$toast({
                                            message:'',
                                            type:'success',
                                            duration:3000,
                                        });
                                    }else if(res.body.data.payment_method==3){
                                        this.$toast({
                                            message:'',
                                            type:'success',
                                            duration:3000,
                                        });
                                    }
                                    this.dragverify = false
                                    this.nextText=''
                                    this.createorderdata=res.body.data
                                    this.active=2
                                    window.setTimeout(function () {
                                        _this.$router.push({
                                            path:'/orderdata',
                                            query:{
                                                id:res.body.data.order_id
                                            }
                                        });
                                    },10000)
                                }
                            }else if(res.body.success==8){
                                if(this.yzm.num <=1){
                                    _this.$toast({
                                        message:'',
                                        type:'fail',
                                        duration:3000,
                                    });
                                    _this.reload();
                                }else {
                                    this.$toast({
                                        message:'',
                                        type:'fail',
                                        duration:3000,
                                    });
                                    this.Supplement=false
                                    this.EBsubdaifubled = false
                                    this.yzm.num --
                                    this.phone_code = ""
                                    this.textcolor = "#f56c6c"
                                    this.msg = ""+this.yzm.num+""
                                }
                            }else if(res.body.success==11){
                                this.$toast({
                                    message:'',
                                    type:'fail',
                                    duration:3000,
                                });
                                _this.$router.push({ path:'/order_list'})
                            }
                        },function(res){

                        });
                    }else {
                        this.Supplement =true
                    }

                }


            },
            //
            BKcreateorderajax:function(){
                var _this=this
                this.nextbeld=true
                this.BKsubdaifubled = true
                //-----------
                var timestamp = Date.parse(new Date());
                var num = '';
                for(var i=0;i<5;i++)
                {
                    num+=Math.floor(Math.random()*10);
                }
                var sign = timestamp + num
                //------------
                this.$http.post('https://www.trillionricher.com/1.0/create_order',{
                    sign:sign,
                    order_id:this.order_id,
                    merber_id:this.merber_id,
                    token:this.OS.token,
                    is_mobile:1,
                    payment_method:this.payment_method,
                    alipay_type:this.alipay_type,
                    service_charge:'',
                    remarks:'',
                    platform_serve:this.platform_serve,//
                    withhold_type:this.withhold_type,
                    payment_code:this.paypwd,
                    invoice_type:this.invoice_type,//
                    CustomerEmail:this.merber_email,
                    CustomerAddr:this.company_add,//
                    CustomerIdentifier:this.company_num,//
                    LoveCode:this.LoveCode,//
                    CustomerName:this.invoice_type==3?this.company_name:this.merber_name,//
                    CarruerNum:this.CarruerNum,//
                },{emulateJSON:true}).then(function(res){
                    this.BKsubdaifubled = false
                    if(res.body.success==-1){
                        this.Logout()
                    }else if(res.body.success===1){
                        this.dragverify = false
                        this.Supplement=false
                        if(this.withhold_type==2){
                            this.loding=true
                            this.chaoshang=res.body.data
                            window.setTimeout(function () {
                                document.getElementById("__ecpayForm").submit();
                            },100)
                        }else{
                            if(res.body.data.payment_method==1 || res.body.data.payment_method==5){
                                this.$toast({
                                    message:'',
                                    type:'success',
                                    duration:3000,
                                });
                            }else if(res.body.data.payment_method==3){
                                this.$toast({
                                    message:'',
                                    type:'success',
                                    duration:3000,
                                });
                            }
                            this.nextText=''
                            this.createorderdata=res.body.data
                            this.active=2
                            window.setTimeout(function () {
                                _this.$router.push({
                                    path:'/orderdata',
                                    query:{
                                        id:res.body.data.order_id
                                    }
                                });
