            cancel_order:function(){
                var _this=this
                if(this.lefttext==''){
                    this.$router.push('/');
                }  else {
                    this.$http.post('https://www.trillionricher.com/1.0/cancel_order',{
                        order_id:this.order_id,
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success===1){
                            this.$toast({
                                message:'',
                                type:'success',
                                duration:1000,
                            });
                            setTimeout(function (){
                                _this.$router.push('/')
                                _this.$router.go(0)
                            },1000);
                        }
                    },function(res){
                        console.log(res);
                    });
                }
            },
            check_order:function () {
                var Unfulfilled_order=this.$route.query.Unfulfilled_order
                if(Unfulfilled_order==true){
                    this.$http.post('https://www.trillionricher.com/1.0/history_order',{
                        merber_id: this.merber_id,
                        token:this.OS.token
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success==-1){
                            this.Logout()
                        }else if(res.body.success==1){
                            this.order_id=res.body.data.order_id
                            this.OrderData=res.body.data
                            this.active=1
                        }
                    },function(res){
                        console.log(res);
                    });
                }else {
                    this.$http.post('https://www.trillionricher.com/1.0/check_order',{
                        merber_id: this.merber_id,
                        token:this.OS.token,
                        service_type: 1
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success==-1){
                            this.Logout()
                        }else if(res.body.success==0){
                            this.$router.push({path: '/zkl'})
                            this.CustomerEmail=res.body.data.merber_email
                        }else if (res.body.success == 3){
                            this.$router.push({path: '/'})
                            this.$toast({
                                message:'',
                                type:'fail',
                                duration:3000,
                            });
                        }else if (res.body.success == 4){
                            this.$router.push({path: '/'})
                            this.$toast({
                                message:'',
                                type:'fail',
                                duration:3000,
                            });
                        }else {
                            this.$router.push({
                                path:'/orderdata',
                                query:{
                                    id:res.body.data.order_id
                                }
                            });
                            this.$toast({
                                message:'',
                                type:'fail',
                                duration:3000,
                            });
                        }
                    },function(res){
                        console.log(res);
                    });
                }
            },
            getphone:function () {
                this.$http.post('https://www.trillionricher.com/1.0/order_bank_alipay',{
                    merber_id: this.merber_id,
                    token:this.OS.token,
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success==-1){
                        this.Logout()
                    } else if(res.body.success==1){
                        this.merber_phone=res.body.data.merber_phone
                    }
                },function(res){
                    console.log(res);
                });
            },
            Openandclose:function () {
                this.$http.post('https://www.trillionricher.com/1.0/notice_switch',{},
                    {emulateJSON:true}).then(function(res){

                    if(res.body.success==1){
                        var oac = res.body.data[2]

                        if(oac.switch == 2){
                            console.log(1);
                            this.$toast({
                                message:oac.content,
                                type:'fail',
                                duration:3000,
                            });
                            this.$router.push({path: '/'})
                            return false
                        }else {
                            if(this.merber_id==undefined){
                                this.$router.push('/login');
                            }else {

                                this.check_order()
                                this.getexchange()
                                this.getphone()
                            }
                        }
                    }
                },function(res){
                    console.log(res);
                });
            }

        },
        computed: {},
        watch: {
            url:function (url) {
                if (url == ''  || /^##.*$/.test(url)) {
                    this.$toast({
                        message:'',
                        type:'fail',
                        duration:1000,
                    });
                }else{
                    this.urlbeld=true
                    this.nextbeld=true
                    this.getexchange()//
                    this.$http.post('https://www.trillionricher.com/1.0/submit_url',{
                        merber_id:this.merber_id,
                        token:this.OS.token,
                        service_type:1,
                        pay_url:this.url,
                        exchange_rate:this.exchange,
                        order_money:0,
                        pay_number:0,
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success==-1){
                            this.Logout()
                        }else if(res.body.success===1){
                            this.nextText=""
                            this.order_id=res.body.data.order_id
                            this.timer()
                        }else if(res.body.success===3){
                            this.$toast({
                                message:'',
                                type:'fail',
                                duration:1000,
                            });
                        }
                    },function(res){
                        console.log(res);
                    });
                }
            }
        },
        created: function () {
            this.Openandclose()
        }

    }
</script>
