<template>
    <div class="login">
        <div class="pagetitle">
            <div>
                <router-link to="/personal">
                    <van-icon name="arrow-left" size="14px"></van-icon>
                    <span style="margin-left: 10px"><b></b></span>
                </router-link>
            </div>
        </div>
        <div class="forgotpassword">
            <div  v-show="wjmmtext">(10-20)</div>
            <van-field
                    v-model="merber_phone"
                    placeholder=""
                    label=""
            >
                <van-button slot="button" size="small" type="primary" :disabled="phoneyzmdata.disabled" @click="fphoneyzm">{{phoneyzmdata.btntext}}</van-button>
            </van-field>
            <van-field
                    v-model="merber_phcode"
                    placeholder="6"
                    label=""
            ></van-field>
            <van-field
                    v-model="merber_password"
                    placeholder=" 10-20 "
                    label=""
                    :icon="pwdicon"
                    :type="pwdtype"
                    @click-icon="showpwd"
            ></van-field>
            <van-field
                    v-model="merber_up_password"
                    placeholder=""
                    label=""
                    :icon="pwdicon"
                    :type="pwdtype"
                    @click-icon="showpwd"
            ></van-field>
            <div style="padding: 0 20px;margin-top: 20px">
                <van-button size="small" block type="primary" small style="height: 40px;line-height: 40px" @click="forget_pad" :disabled="forget_padbeld">  </van-button>
            </div>
        </div>

        <van-dialog v-model="wjmmdragverify" title="" :showConfirmButton="false" :closeOnClickOverlay="true">
            <div style="padding: 30px 10px;font-size: 18px;font-weight: bold;margin: 0 auto"  >
                <slide-verify :l="42"
                              :r="10"
                              :w="270"
                              :h="155"
                              :imgs="imgs"
                              :accuracy="3"
                              ref="slidewjmm"
                              slider-text=" >>>"
                              style="margin: 0 auto"
                              @success="wjmmonSuccess"
                              @fail="wjmmonFail"
                              @refresh="wjmmonRefresh"
                              @again="wjmmonAgain"
                ></slide-verify>
                <div class="slide-verify" :style="{color:textcolor}" >{{wjmmmsg}}</div>
            </div>
        </van-dialog>
    </div>
</template>

<script>
    import Global from './Global'
    export default {
        data: function () {
            return {
                dragverify:false, //
                wjmmtext:false,//
                wjmmdragverify:false,//
                msg: '',   //
                wjmmmsg:'',//
                textcolor:'#46ce04', //
                imgs:Global.imgs, //
                url:"https://www.trillionricher.com/1.0/forget_pad/",
                forget_padbeld:true,//
                email_phone:'',//
                password:'', //
                pwdicon:'closed-eye',
                pwdtype:'password',
                isPreservation:true,
                wjmmshow:false,
                merber_phone:'',
                merber_phcode:'',
                merber_password:'',
                merber_up_password:'',
                phoneyzmdata:{
                    disabled:false,
                    btntext:'',
                    time:'60',
                    num:3,
                },
            }
        },
        components: {},
        methods: {
            //
            wjmmonSuccess(times){
                var _this=this
                this.forget_padbeld = false
                this.textcolor = "#46ce04"
                this.wjmmmsg = ''+(times / 1000).toFixed(1)+'s'
                if(this.merber_phone==''){
                    this.$toast({
                        message:'',
                        type:'fail',
                        duration:1000,
                    });
                }else {

                    this.$http.post('https://www.trillionricher.com/1.0/forget_pad_code',{
                        merber_phone:this.merber_phone,
                    },{emulateJSON:true}).then(function(res){
                        this.wjmmdragverify = false
                        this.$refs.slidewjmm.reset();
                        if(res.body.success===1){
                            this.phoneyzmdata.disabled=true
                            this.phoneyzmdata.time=60
                            this.timer();
                            this.$toast({
                                message:'',
                                type:'success',
                                duration:1000,
                            });
                        }else if(res.body.success===2){
                            this.$toast({
                                message:'',
                                type:'fail',
                                duration:1000,
                            });
                        }else {
                            this.$toast({
                                message:'',
                                type:'fail',
                                duration:1000,
                            });
                        }
                    },function(res){
                        console.log(res);
                    });
                }

            },
            //
            wjmmonFail(){
                this.textcolor = "#f56c6c"
                this.msg = '!'
            },
            //
            wjmmonRefresh(){
                this.msg = ''
            },
            //
            wjmmonAgain() {
                console.log('');
                this.msg = 'try again';
                // 
                this.$refs.slideblock.reset();
            },
            onClickLeft:function(){
                this.$router.push('/');
            },
            showpwd:function () {
                if(this.pwdtype==='password'){
                    this.pwdtype='text'
                    this.pwdicon='eye-o'
                }else {
                    this.pwdtype='password'
                    this.pwdicon='closed-eye'
                }
            },


            timer() {
                if (this.phoneyzmdata.time > 0) {
                    this.phoneyzmdata.time--;
                    this.phoneyzmdata.btntext=this.phoneyzmdata.time+"s";
                    setTimeout(this.timer, 1000);
                } else{
                    this.phoneyzmdata.time=60;
                    this.phoneyzmdata.btntext="";
                    this.phoneyzmdata.disabled=false;
                }
            },
            fphoneyzm:function(){
                if(this.merber_phone==''){
                    this.$toast({
                        message:'',
                        type:'fail',
                        duration:1000,
                    });
                }else {
                    this.wjmmdragverify = true
                }
            },
            forget_pad:function () {
                var _this = this
                var phoneReg = /^([0][9])\d{8}$/
                var pwdReg=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z_]{10,20}$/
                if(this.merber_phone=='' || this.merber_phcode=='' || this.merber_password=='' || this.merber_up_password==''){
                    this.$toast({
                        message:'',
                        type:'fail',
                        duration:2000,
                    });
                }else if(!phoneReg.test(this.merber_phone)){
                    this.$toast({
                        message:'',
                        type:'fail',
                        duration:2000,
                    });
                }else if(!pwdReg.test(this.merber_password)){
                    this.$toast({
                        message:'10-20',
                        type:'fail',
                        duration:2000,
                    });
                }else if(this.merber_password !== this.merber_up_password){
                    this.$toast({
                        message:'!',
                        type:'fail',
                        duration:2000,
                    });
                }else {
                    this.forget_padbeld = true
                    this.$http.post(this.url,{
                        merber_password:this.merber_password,
                        merber_phone:this.merber_phone,
                        merber_phcode:this.merber_phcode
                    },{emulateJSON:true}).then(function(res){
                        this.forget_padbeld = false
                        if(res.body.success===1){
                            _this.$toast({
                                message:'',
                                type:'success',
                                duration:2000,
                            });
                            this.$router.push('/login');
                        }else if(res.body.success==2){
                            _this.$toast({
                                message:'',
                                type:'fail',
                                duration:2000,
                            });
                        }else if(res.body.success==3){
                            if(this.phoneyzmdata.num <=1 ){
                                _this.$toast({
                                    message:"",
                                    type:'fail',
                                    duration:2000,
                                });
                                this.reload();
                            }else {
                                _this.merber_phcode==''
                                _this.phoneyzmdata.num--
                                _this.$toast({
                                    message:""+this.phoneyzmdata.num+"",
                                    type:'fail',
                                    duration:3000,
                                });
                            }
                        }else if(res.body.success==4){
                            _this.$toast({
                                message:'',
                                type:'fail',
                                duration:2000,
                            });
                        }else {
                            _this.$toast({
                                message:'',
                                type:'fail',
                                duration:2000,
                            });
                        }
                    },function(res){
                        console.log(res);
                    });
                }
            }
        },
        computed: {

        },
        watch: {},
        mounted: function () {

        }
    }
</script>
