<template>
    <div class="substitute" v-loading="product_order_loading" :element-loading-text="product_order_text"  element-loading-background="rgba(0, 0, 0, 0.7)">
        <div class="pagetitle">
            <div>
                <router-link to="/">
                    <van-icon name="arrow-left" size="14px"></van-icon>
                    <span style="margin-left: 10px"><b></b></span>
                </router-link>
            </div>
        </div>
        <div class="substitute_body">
            <div class="sub_select">
                <span class="subtype_title"></span>
                <el-select v-model="alipay_type" placeholder="" size="small">
                    <el-option
                            class="optinlist"
                            v-for="item in typelist"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <div class="sub_select">
                <span class="subtype_title"></span>
                <van-field
                        v-model="userRMB"
                        clearable
                        placeholder=""
                >
                </van-field>
            </div>
            <div class="getalipay">
                <van-button size="small" style="background-color: #00A0E9"  @click="getalipay"></van-button>
                <van-button size="small" style="background-color: #A40000"  @click="getalipay"></van-button>
            </div>
            <div class="sub_select" v-show="showalipay">
                <span class="subtype_title"></span>
                <van-cell class="zfb"   @click="Cope" :value="zfb" :data-clipboard-text='zfb'>
                    <van-icon slot="right-icon"  class="custom-icon" ></van-icon>
                </van-cell>
            </div>
            <div class="sub_select">
                <span class="subtype_title"></span>
                <van-field  v-model="url" placeholder=""  :disabled="urlbeld" />
            </div>
            <div class="subnest">
                <van-button size="small" block style="background-color: #EFB300;border-radius: 10px" @click="gopay" :disabled="BKsubdaifubled"  ></van-button>
            </div>

        </div>
        <div v-show="loding" style="width:100%;height: 100%;z-index: 9999;position:fixed;top: 0;background-color:rgba(0,0,0,0.4)">
            <div style="position: relative;top:45%;text-align: center;width: 100%">
                <van-loading color="#fff" style="margin: 0 auto " size="40px"/>
            </div>
        </div>

    </div>
</template>

<script>
    import Clipboard from 'clipboard';
    let rst=new Clipboard('.zfb');
    export default {
        inject:['reload'],
        data: function () {
            return {
                showalipay:false,
                Supplement:false,
                withhold_type:'1',
                msg: '',   //
                textcolor:'#46ce04', //
                toolTipbox:false,
                dragverify:false, //
                slideshow:true,//
                EBsubdaifubled:true,//E
                BKsubdaifubled:false,
                phone_code:'',//E
                platform_serve:20,//
                yzm:{
                    text:'',
                    time:60,
                    bled:false,
                    num:3
                },
                merber_phone:'',
                merber_phcode:'',
                typelist:[
                    {
                        value:1,
                        label:''
                    },
                    {
                        value:2,
                        label:''
                    },
                    {
                        value:10,
                        label:''
                    }
                ],//

                merber_id:Cookies.get('merber_id'),
                merber_name:Cookies.get('merber_name'),
                merber_email:Cookies.get('merber_email'),

                loding:false,
                chaoshang:'',
                lefttext:"",
                pengyouzh:false,
                R_ye:'2321',
                zfb:'',  //
                active:0,   //
                alipay_type:'',//1 2
                userRMB:'',//
                url:'',//URL
                payment_method:'',//
                product_order_text:'',
                product_order_loading:false,
                exchange:'',//
                order_id:'',//
                urlbeld:false,//url
                nextbeld:false,//
                nextText:'',//
                second:60,//
                OrderData:'' ,//
                createorderdata:{}//


            }
        },
        components: {},
        methods: {

            gopay:function(){
                var re =/^\d+(\.\d+)?$/;
                var reurl1 = /^https:\/\/shenghuo.alipay.com:443\/peerpaycore\/confirmPeerPay.htm\?id=.*$/
                var reurl2 = /^https:\/\/shenghuo.aliphtay.com:443\/peerpaycore\/confirmPeerPay.htm\?id=.*$/
                var reurl3 = /^https:\/\/shenghuo.alipay.com\/peerpaycore\/confirmPeerPay.htm\?id=.*$/
                var reurl4= /^##.*$/
                var reurl5 = /^APP{1}[0-9a-zA-Z]{10,}$/

                if(this.alipay_type == ''){
                    this.$toast({
                        message:'',
                        type:'fail',
                        duration:1000,
                    });
                    return false
                }
                if(this.userRMB=='' || !re.test(this.userRMB)){
                    this.$toast({
                        message:'',
                        type:'fail',
                        duration:1000,
                    });
                    return false
                }
                if(this.zfb == ''){
                  this.$toast({
                    message:'',
                    type:'fail',
                    duration:1000,
                  });
                  return false
                }
                if (!reurl1.test(this.url) && !reurl2.test(this.url) && !reurl3.test(this.url)){
                    this.$toast({
                        // message:'',
                        message:',',
                        type:'fail',
                        duration:1000,
                    });
                    return false
                }
                this.product_order_loading = true
                this.$http.post('https://www.trillionricher.com/1.0/submit_url',{
                    merber_id:this.merber_id,
                    token:this.OS.token,
                    service_type:1,
                    pay_url:this.url,
                    exchange_rate:this.exchange,
                    order_money:this.userRMB,
                    pay_number:this.zfb
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success==-1){
                        this.Logout()
                    }else if(res.body.success===1){
                        this.order_id=res.body.data.order_id
                        this.product_order_text=""
                        this.timer()
                    }else if(res.body.success===3){
                        this.$toast({
                            message:'',
                            type:'fail',
                            duration:1000,
                        });
                    }
                },function(res){
                    console.log(res);
                });
            },
            Cope:function () {
                this.$toast({
                    message:"",
                    type:"success",
                    duration:1000
                })
            },
            submit_url:function(){
                this.urlbeld=true
                this.nextbeld=true
                this.getexchange()//
                this.$http.post('https://www.trillionricher.com/1.0/submit_url',{
                    merber_id:this.merber_id,
                    token:this.OS.token,
                    service_type:1,
                    pay_url:this.url,
                    exchange_rate:this.exchange,
                    order_money:this.userRMB,
                    pay_number:this.zfb
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success==-1){
                        this.Logout()
                    }else if(res.body.success===1){
                        this.order_id=res.body.data.order_id
                        this.nextText=""
                        this.timer()
                    }else if(res.body.success===3){
                        this.$toast({
                            message:'',
                            type:'fail',
                            duration:1000,
                        });
                    }
                },function(res){
                    console.log(res);
                });
            },
            timer:function () {
                var _this=this
                if(this.second>0){
                    this.second--;
                    this.product_order_text = ` ${this.second} `;
                    this.$http.post('https://www.trillionricher.com/1.0/product_order',{
                        merber_id:this.merber_id,
                        token:this.OS.token,
                        order_id:this.order_id
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success===0){
                            if(this.second>0){
                                setTimeout(this.timer, 1000);
                            }else {
                                if(this.$route.path=='/substitute'){
                                    toast.clear();
                                    this.$dialog.alert({
                                        message: '<span style="color: red;font-weight: bold">'+res.body.data.line+'</span> <span style="color: red;font-weight: bold">'+this.Transformation(res.body.data.line)+'</span>',
                                        showCancelButton:true,
                                        confirmButtonText:'',
                                        cancelButtonText: '',
                                    }).then(() => {
                                        _this.$router.push({
                                            path:'/substitute_two',
                                            query:{
                                                id:res.body.data.order_id
                                            }
                                        });
                                    }).catch(() => {
                                        _this.second=60
                                        setTimeout(this.timer, 1000);
                                    });
                                }
                            }

                        }else if(res.body.success===2){
                            toast.clear()
                            this.$dialog.alert({
                                message: ''
                            }).then(()=>{
                                    location.reload()
                                }
                            );
                        }else if(res.body.success===1){
                            this.$router.push({
                                path:'/substitute_two',
                                query:{
                                    id:res.body.data.order_id
                                }
                            });
                        }else if(res.body.success===3){
                            _this.$router.push({
                                path:'/orderdata',
                                query:{
                                    id:Number(res.msg)
                                }
                            });
                            this.$toast({
                                message:'5000',
                                type:'fail',
                                duration:3000,
                            });

                        }
                    },function(res){
                        console.log(res);
                    });
                }
            },
            //
            getalipay:function () {
                var re =/^\d+(\.\d+)?$/;
                if(this.alipay_type==''){
                    this.$toast({
                        message:'',
                        type:'fail',
                        duration:1000,
                    });
                }else if(this.userRMB=='' || !re.test(this.userRMB)){
                    this.$toast({
                        message:'',
                        type:'fail',
                        duration:1000,
                    });
                }else {
                    this.$http.post('https://www.trillionricher.com/1.0/get_alipay',{
                        merber_id:this.merber_id,
                        token:this.OS.token,
                        alipay_type:this.alipay_type,
                        robot_rmb:this.userRMB,
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success==-1){
                            this.Logout()
                        }else if(res.body.success===1){
                            this.showalipay = true
                            this.pengyouzh=true
                            this.zfb=res.body.data
                        }
                    },function(res){
                        console.log(res);
                    });
                }
            },
            //
            getexchange:function(){
                this.$http.post('https://www.trillionricher.com/1.0/exchange',{
                    exchange_type:1,
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success===1){
                        this.exchange=res.body.data[0].exchange
                    }
                },function(res){
                    console.log(res);
                });
            },


            Transformation:function(line){
                line=line-1
                var s=parseInt((line*10+120))
                var h=Math.floor(s/60);
                if(s>60){
                    return h+''+(s%60)+''
                }else {
                    return s+'';
                }
            },
            history_order:function(){
                this.$http.post('https://www.trillionricher.com/1.0/history_order',{
                    merber_id: this.merber_id,
                    token:this.OS.token
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success==-1){
                        this.Logout()
                    }else if(res.body.success==1){
                        this.$router.push({
                            name: 'Two',
                            params:{
                                id:res.body.data.order
                            }
                        });
                        this.$toast({
                            message:'',
                            type:'fail',
                            duration:3000,
                        });
                    }
                },function(res){
                    console.log(res);
                });
            },
            check_order:function(){
                this.$http.post('https://www.trillionricher.com/1.0/check_order',{
                    merber_id: this.merber_id,
                    token:this.OS.token,
                    service_type: 1
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success==-1){
                        this.Logout()
                        return false
                    }
                    if(res.body.success==0){
                        this.$router.push({path: '/substitute'})
                        this.CustomerEmail=res.body.data.merber_email
                    }
                    if (res.body.success == 3){
                        this.$router.push({path: '/'})
                        this.$toast({
                            message:'',
                            type:'fail',
                            duration:3000,
                        });
                        return false
                    }
                    if (res.body.success == 4){
                        this.$router.push({path: '/'})
                        this.$toast({
                            message:'',
                            type:'fail',
                            duration:3000,
                        });
                        return false
                    }
                },function(res){
                    console.log(res);
                });
            },

            Openandclose:function () {
                this.$http.post('https://www.trillionricher.com/1.0/notice_switch',{},
                {emulateJSON:true}).then(function(res){

                    if(res.body.success==1){
                        var oac = res.body.data[2]

                        if(oac.switch == 1){
                            this.$toast({
                                message:oac.content,
                                type:'fail',
                                duration:3000,
                            });
                            this.$router.push({path: '/'})
                            return false
                        }else {
                            if(this.merber_id==undefined){
                                this.$router.push('/login');
                            }else {
                                this.check_order()
                                this.getexchange()
                                this.history_order()
                            }
                        }
                    }
                },function(res){
                    console.log(res);
                });
            }

        },
        computed: {},
        watch: {
        },
        created: function () {
            this.Openandclose()
        }
    }
</script>
