                merber_name:Cookies.get('merber_name'),
                merber_email:Cookies.get('merber_email'),

                loding:false,
                chaoshang:'',
                lefttext:"",
                pengyouzh:false,
                R_ye:'2321',
                zfb:'',  //
                active:0,   //
                alipay_type:'',//1 2
                userRMB:'',//
                url:'',//URL
                payment_method:'',//

                donation_num:[
