        components: {},
        methods: {
            goindex(){
                this.toolTipbox = false
                this.$router.push('/')
            },
            //
            onSuccess(times){
                this.msg = ""
                this.textcolor = "#46ce04"
                this.slideshow = true
                this.EBsubdaifubled = false
                // this.phonetimer()
                // this.$toast({
                //     message:'',
                //     type:'success',F
                //     duration:1000,
                // });
                this.$http.post('https://www.trillionricher.com/1.0/get_check',{
                    merber_id:this.OS.merber_id,
                    token:this.OS.token,
                    merber_phone:this.merber_phone
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success==2){
                        this.$toast({
                            message: '',
                            type: 'fail',
                            duration: 2000,
                        })
                    }else if(res.body.success==1){
                        this.msg = ""
                        this.phonetimer()
                        this.$toast({
                            message:'',
                            type:'success',
                            duration:1000,
                        });
                    }
                },function(res){
                });
            },
            //
            onFail(){
                this.textcolor = "#f56c6c"
                this.msg = '!'
            },
            //
            onRefresh(){
                this.msg = ''
            },
            //
            onAgain() {
                this.msg = 'try again';
                // 
                this.$refs.slideblock.reset();
            },

            phonetimer() {
                this.yzm.bled=true
                if (this.yzm.time > 0) {
                    this.yzm.time--;
                    this.yzm.text=this.yzm.time+"s";
                    setTimeout(this.phonetimer, 1000);
                } else{
                    this.yzm.time=0;
                    this.yzm.text="";
                    this.yzm.bled=false;
                }
            },
            phoneyzm:function(){
                this.phonetimer()
                this.$http.post('https://www.trillionricher.com/1.0/forget_pad_code/',{
                    merber_phone:this.merber_phone,
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success==2){
                        this.$toast({
                            message: '',
                            type: 'fail',
                            duration: 2000,
                        })
                    }else if(res.body.success==1){
                        this.$toast({
                            message:'',
                            type:'success',
                            duration:1000,
                        });
                    }
                },function(res){
                });
            },



            Other_ways:function () {
                this.waysshow=true
            },
            cancel_order:function(){
                this.$dialog.alert({
                    message: '',
                    showCancelButton:true,
                    confirmButtonText:'',
                }).then(() => {
                    this.$http.post('https://www.trillionricher.com/1.0/cancel_order',{
                        order_id:this.orderdata.order_id,
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success===1){
                            this.$toast({
                                message:'',
                                type:'success',
                                duration:1500,
                            });
                            this.$router.push({path:'/'})
                        }else {
                            this.$toast({
                                message:'!',
                                type:'fail',
                                duration:1500,
                            });
                        }
                    },function(res){
                    });
                }).catch(() => {
                    // on cancel
                });
            },

            Transformation:function(line){
                line=line-1
                var s=parseInt((line*10+120))
                var h=Math.floor(s/60);
                if(s>60){
                    return h+''+(s%60)+''
                }else {
                    return s+'';
                }
            },

            // 
            createorder:function () {
                    if (this.payment_method==''){
                        this.$toast({
                            message:"",
                            type:"fail",
                            duration:1000
                        })
                        return false
                    }
                    if((this.payment_method == 7 || this.payment_method == 9 ) && this.bankcode.length !== 5){
                        this.$toast({
                            message:' ',
                            type:'fail',
                            duration:1000,
                        });
                        return false
                    }
                   this.createorder_ajax()
            },
            //
            createorder_ajax:function(){
                var _this=this
                var twd=this.orderdata.robot_rmb*this.exchange
                if(this.payment_method==2 && twd>5970){
                    this.$toast({
                        message:'5970',
                        type:'fail',
                        duration:3000,
                    });
                }else if(this.payment_method==3){
                    if(Number(this.orderdata.r_money) <=0 ){
                        this.$toast({
                            message:'E,',
                            type:'fail',
                            duration:3000,
                        });
                    }else {
                        this.dragverify = true
                    }
                }else {
                    this.nextbeld=true
                    //-----------
                    var timestamp = Date.parse(new Date());
                    var num = '';
                    for(var i=0;i<5;i++)
                    {
                        num+=Math.floor(Math.random()*10);
                    }
                    var sign = timestamp + num
                    //------------
                    this.$http.post('https://www.trillionricher.com/1.0/create_order',{
                        sign:sign,
                        order_id:this.orderdata.order_id,
                        merber_id:this.merber_id,
                        token:this.OS.token,
                        is_mobile:1,
                        payment_method:this.payment_method,
                        alipay_type:this.alipay_type,
                        service_charge:'',
                        remarks:'',
                        bankcode:this.bankcode,
                        platform_serve:this.platform_serve,//
                        invoice_type:'',//
                        CustomerEmail:this.merber_email,
                        CustomerAddr:this.company_add,//
                        CustomerIdentifier:this.company_num,//
                        LoveCode:this.LoveCode,//
                        CustomerName:'',//
                        CarruerNum:this.CarruerNum,//
                    },{emulateJSON:true}).then(function(res){
                        if(res.body.success==-1){
                            this.Logout()
                        }else if(res.body.success===1){
                            if(this.payment_method==2){
                                this.loding=true
                                this.chaoshang=res.body.data
                                window.setTimeout(function () {
                                    document.getElementById("__ecpayForm").submit();
                                },100)
                            }else{
                                if(res.body.data.payment_method==1){
                                    _this.$toast({
                                        message:'',
                                        type:'success',
                                        duration:3000,
                                    });
                                }else if(res.body.data.payment_method==3){
                                    _this.$toast({
                                        message:'',
                                        type:'success',
                                        duration:3000,
                                    });
                                }
                                this.nextText=''
                                this.createorderdata=res.body.data
                                this.active=2
                                window.setTimeout(function () {
                                    _this.$router.push({
                                        path:'/orderdata',
                                        query:{
                                            id:res.body.data.order_id
                                        }
                                    });
                                },10000)
                            }
                        }else if(res.body.success==4){
                            this.$dialog.alert({
                                message: 'E,',
                                showCancelButton:true,
                                confirmButtonText:'',
                                cancelButtonText: '',
                            }).then(() => {
                                _this.$router.push({ path:'/rbcz'})
                            }).catch(() => {
                                // on cancel
                            });
                        }else if(res.body.success==11){
                            this.$toast({
                                message:'',
                                type:'fail',
                                duration:3000,
                            });
                            _this.$router.push({ path:'/order_list'})
                        }else if(res.body.success==9){
                          this.$toast({
                            message:'',
                            type:'fail',
                            duration:3000,
                          });
                        }
                    },function(res){

                    });
                }
            },
            //E
            EBcreateorderajax:function(){
                if(this.phone_code.length !== 6){
                    this.$toast({
                        message:"",
                        type:"fail",
                        duration:1000
                    })
                }else {
                    if(Number(this.orderdata.r_money) >= Number(this.orderdata.robot_rmb)){
                        var _this=this
                        this.nextbeld=true
                        this.EBsubdaifubled = true
                        //-----------
                        var timestamp = Date.parse(new Date());
                        var num = '';
                        for(var i=0;i<5;i++)
                        {
                            num+=Math.floor(Math.random()*10);
                        }
                        var sign = timestamp + num
                        //------------
                        this.$http.post('https://www.trillionricher.com/1.0/create_order',{
                            sign:sign,
                            order_id:this.orderdata.order_id,
                            merber_id:this.merber_id,
                            token:this.OS.token,
                            is_mobile:1,
                            payment_method:this.payment_method,
                            alipay_type:this.alipay_type,
                            service_charge:'',
                            platform_serve:this.platform_serve,//

                            remarks:'',
                            withhold_type:this.withhold_type,
                            payment_code:this.phone_code,
                            invoice_type:'',//
                            CustomerEmail:this.merber_email,
                            merber_phone:this.merber_phone,
                            CustomerAddr:this.company_add,//
                            CustomerIdentifier:this.company_num,//
                            LoveCode:this.LoveCode,//
                            CustomerName:'',//
                            CarruerNum:this.CarruerNum,//
                        },{emulateJSON:true}).then(function(res){
                            this.loding=false
                            this.EBsubdaifubled = false
                            if(res.body.success==-1){
                                this.Logout()
                            }else if(res.body.success===1){
                                this.dragverify = false

                                this.$toast({
                                    message:'',
                                    type:'success',
                                    duration:3000,
                                });
                                this.$router.push({
                                  path:'/orderdata',
                                  query:{
                                    id:res.body.data.order_id
                                  }
                                });
                            }else if(res.body.success==8){
                                if(this.yzm.num <=1){
                                    _this.$toast({
                                        message:'',
                                        type:'fail',
                                        duration:3000,
                                    });
                                    _this.reload();
                                }else {
                                    this.$toast({
                                        message:'',
                                        type:'fail',
                                        duration:3000,
                                    });
                                    this.Supplement=false
                                    this.EBsubdaifubled = false
                                    this.yzm.num --
                                    this.phone_code = ""
                                    this.textcolor = "#f56c6c"
                                    this.msg = ""+this.yzm.num+""
                                }

                            }else if(res.body.success==11){
                                this.$toast({
                                    message:'',
                                    type:'fail',
                                    duration:3000,
                                });
                                _this.$router.push({ path:'/order_list'})
                            }else if(res.body.success==9){
                              this.$toast({
                                message:'',
                                type:'fail',
                                duration:3000,
                              });
                            }
                        },function(res){

                        });
                    }else {
                        this.Supplement =true
                    }
                }


            },
            //
            BKcreateorderajax:function(){
                var _this=this
                this.nextbeld=true
                this.BKsubdaifubled = true
                this.payment_method = 5
                if((this.withhold_type == "9" || this.withhold_type == "7" )&& this.bankcode.length !== 5){
                    this.$toast({
                        message:'',
                        type:'fail',
                        duration:3000,
                    });
                    return false
                }
                //-----------
                var timestamp = Date.parse(new Date());
                var num = '';
                for(var i=0;i<5;i++)
                {
                    num+=Math.floor(Math.random()*10);
                }
                var sign = timestamp + num
                //------------

                var w_rmb = Number(this.orderdata.robot_rmb)-Number(this.orderdata.r_money).toFixed(2)
                var w_twd=Math.ceil(w_rmb*this.exchange)
                this.$http.post('https://www.trillionricher.com/1.0/create_order',{
                    sign:sign,
                    order_id:this.orderdata.order_id,
                    merber_id:this.merber_id,
                    token:this.OS.token,
                    is_mobile:1,
                    payment_method:this.payment_method,
                    alipay_type:this.alipay_type,
                    service_charge:'',
                    remarks:'',
                    bankcode:this.bankcode,
                    platform_serve:this.platform_serve,//
                    withhold_type:this.withhold_type,
                    withhold_rmb:w_rmb,
                    withhold_twd:w_twd,
                    remain_rmb:this.orderdata.r_money,

                    payment_code:this.phone_code,
                    invoice_type:'',//
                    CustomerEmail:this.merber_email,
                    merber_phone:this.merber_phone,
                    CustomerAddr:this.company_add,//
                    CustomerIdentifier:this.company_num,//
                    LoveCode:this.LoveCode,//
                    CustomerName:'',//
                    CarruerNum:this.CarruerNum,//
                },{emulateJSON:true}).then(function(res){
                    this.BKsubdaifubled = false
                    if(res.body.success==-1){
                        this.Logout()
                    }else if(res.body.success===1){
                        this.Supplement=false
                        this.dragverify = false
                        if(this.withhold_type==2){
                            this.loding=true
                            this.chaoshang=res.body.data
                            window.setTimeout(function () {
                                document.getElementById("__ecpayForm").submit();
                            },100)
                        }else{
                            this.$toast({
                                message:'',
                                type:'success',
                                duration:3000,
                            });
                            this.$router.push({
                              path:'/orderdata',
                              query:{
                                id:res.body.data.order_id
                              }
                            });
                        }
                    }else if(res.body.success==8){
                        if(this.yzm.num <=1){
                            _this.$toast({
                                message:'',
                                type:'fail',
                                duration:3000,
                            });
                            _this.reload();
                        }else {
                            this.$toast({
                                message:'',
                                type:'fail',
                                duration:3000,
                            });
                            this.Supplement=false
                            this.EBsubdaichubled = false
                            this.yzm.num --
                            this.phone_code = ""
                            this.textcolor = "#f56c6c"
                            this.msg = ""+this.yzm.num+""
                        }

                    }else if(res.body.success==9){
                      this.$toast({
                        message:'',
                        type:'fail',
                        duration:3000,
                      });
                    }else if(res.body.success==11){
                        this.$toast({
                            message:'',
                            type:'fail',
                            duration:3000,
                        });
                        _this.$router.push({ path:'/order_list'})
                    }
                },function(res){

                });
            },
            getphone:function () {
                this.$http.post('https://www.trillionricher.com/1.0/order_bank_alipay',{
                    merber_id: this.merber_id,
                    token:this.OS.token,
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success==-1){
                        this.Logout()
                    } else if(res.body.success==1){
                        this.merber_phone=res.body.data.merber_phone
                    }
                },function(res){
                });
            },
            Openandclose:function () {
                this.$http.post('https://www.trillionricher.com/1.0/notice_switch',{},
                    {emulateJSON:true}).then(function(res){

                    if(res.body.success==1){
                        var oac = res.body.data[2]

                        if(oac.switch == 2){
                            this.$toast({
                                message:oac.content,
                                type:'fail',
                                duration:3000,
                            });
                            this.$router.push({path: '/'})
                            return false
                        }else {
                            if(this.merber_id==undefined){
                                this.$router.push('/login');
                            }else {
                                this.check_order()
                                this.getexchange()
                                this.getphone()
                            }
                        }
                    }
                },function(res){
                });
            },
            getorderdata:function(id){
                this.$http.post('https://www.trillionricher.com/1.0/product_order',{
                    merber_id:this.merber_id,
                    token:this.OS.token,
                    order_id:id
                },{emulateJSON:true}).then(function(res){
                        if(res.body.success===1){
                            this.orderdata = res.body.data
                        }
                })
            },
            getsuborder:function () {
                var id = this.$route.params.id
                if(id == undefined){
                    this.$router.push({
                        path:'/substitute',
                    });
                }
                this.getorderdata(id)
            }
        },
        computed: {},
        watch: {},
        mounted: function () {
            this.getsuborder()
            this.getphone()
        }
    }
</script>
