<template>
    <div class="personal">
        <div class="pagetitle">
            <div>
                <router-link to="/personal">
                    <van-icon name="arrow-left" size="14px"></van-icon>
                    <span style="margin-left: 10px"><b></b></span>
                </router-link>
            </div>
        </div>
        <van-field
                v-model="merber_password"
                placeholder=""
                label=""
                :icon="pwdicon"
                :type="pwdtype"
                @click-icon="showpwd"
        ></van-field>
        <van-field
                v-model="phoneyzm"
                placeholder=""
                maxlength="6"
                label=""
        >
            <van-button slot="button" size="small" type="primary" :disabled="phoneyzmdata.disabled" @click="dragverify = true">{{phoneyzmdata.btntext}}</van-button>
        </van-field>
        <van-field
                v-model="merber_up_password"
                placeholder=""
                label=""
                :icon="pwdicon"
                :type="pwdtype"
                @click-icon="showpwd"
        ></van-field>
        <van-field
                v-model="merber_up__password"
                placeholder=""
                label=""
                :icon="pwdicon"
                :type="pwdtype"
                @click-icon="showpwd"
        ></van-field>
        <div style="padding: 0 15px;margin-top: 20px">
            <van-button  size="small" type="primary" small block  class="uodata_bt" @click="uppwd" :disabled="uppwdbled"> </van-button>
        </div>
        <div class="toast"></div>
        <van-dialog v-model="dragverify" title="" :showConfirmButton="false" :closeOnClickOverlay="true">
            <div style="padding: 30px 10px;font-size: 18px;font-weight: bold;margin: 0 auto"  >
                <slide-verify :l="42"
                              :r="10"
                              :w="270"
                              :h="155"
                              :imgs="OS.imgs"
                              :accuracy="3"
                              ref="slideblock"
                              slider-text=" >>>"
                              style="margin: 0 auto"
                              @success="onSuccess"
                              @fail="onFail"
                              @refresh="onRefresh"
                              @again="onAgain"
                ></slide-verify>
                <div class="slide-verify" :style="{color:textcolor}" >{{msg}}</div>
            </div>
        </van-dialog>
    </div>
</template>

<script>
    export default {
        inject:['reload'],
        data: function () {
            return {
                msg: '',   //
                textcolor:'#46ce04', //
                pwdicon:'closed-eye',
                pwdtype:'password'  ,//
                merber_password:'',//
                merber_up_password:'',//
                merber_up__password:'',//
                phoneyzm:'',//
                phoneyzmdata:{
                    disabled:false,
                    btntext:'',
                    time:'60',
                    num:3
                },
                uphone:this.$route.params.phone,
                dragverify:false,//
                merber_phone:'',//
                uppwdbled:true,
            }
        },
        components: {},
        methods: {
            onSuccess(times){
                this.dragverify = false
                this.uppwdbled = false
                this.$http.post('https://www.trillionricher.com/1.0/forget_pad_code',{
                    merber_phone:this.merber_phone,
                },{emulateJSON:true}).then(function(res){
                    this.$refs.slideblock.reset();
                    if(res.body.success===1){
                        this.phoneyzmdata.disabled=true
                        this.phoneyzmdata.time=60
                        this.timer();
                        this.$toast({
                            message:'',
                            type:'success',
                            duration:1000,
                        });
                    }else {
                        this.$toast({
                            message:'',
                            type:'fail',
                            duration:1000,
                        });
                    }
                },function(res){
                    console.log(res);
                });
            },
            onFail(){
                this.textcolor = "#f56c6c"
                this.msg = '!'
            },
            onRefresh(){
                this.msg = ''
            },
            onAgain() {
                console.log('');
                this.msg = 'try again';
                // 
                this.$refs.slideblock.reset();
            },
            timer() {
                if (this.phoneyzmdata.time > 0) {
                    this.phoneyzmdata.time--;
                    this.phoneyzmdata.btntext=this.phoneyzmdata.time+"s";
                    setTimeout(this.timer, 1000);
                } else{
                    this.phoneyzmdata.time=0;
                    this.phoneyzmdata.btntext="";
                    this.phoneyzmdata.disabled=false;
                }
            },
            showpwd:function () {
                if(this.pwdtype==='password'){
                    this.pwdtype='text'
                    this.pwdicon='eye-o'
                }else {
                    this.pwdtype='password'
                    this.pwdicon='closed-eye'
                }
            },
            uppwd:function(){
                var pwdReg=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z_]{10,20}$/
                if(!pwdReg.test( this.merber_up_password)){
                    this.$toast({
                        message:'10-20',
                        type:'fail',
                        duration:2000,
                    });
                }else if(this.merber_up_password!==this.merber_up__password){
                    this.$toast({
                        message:'!',
                        type:'fail',
                        duration:2000,
                    })
                }else {
                    this.uppwdbled  = true
                    this.$http.post('https://www.trillionricher.com/1.0/up_pwd',{
                        merber_id:this.OS.merber_id,
                        token:this.OS.token,
                        merber_password:this.merber_password,
                        merber_phcode:this.phoneyzm,
                        merber_up_password:this.merber_up_password
                    },{emulateJSON:true}).then(function(res){
                        this.uppwdbled  = false
                        if(res.body.success==-1){
                            this.$toast({
                                message:'',
                                type:'fail',
                                duration:3000,
                            });
                            this.$router.push({path: '/login'})
                            this.Logout()
                        }else if(res.body.success==0){
                            this.$toast({
                                message:'',
                                type:'fail',
                                duration:1000,
                            });
                        }else if(res.body.success==5){
                            this.$toast({
                                message:res.body.msg,
                                type:'fail',
                                duration:1000,
                            });
                        }else if(res.body.success==1){
                            this.$toast({
                                message:'',
                                type:'success',
                                duration:1000,
                            });
                            this.$router.push('/account');
                        }else if(res.body.success==3){
                            if(this.phoneyzmdata.num <=1 ){
                                this.$toast({
                                    message:"",
                                    type:'fail',
                                    duration:2000,
                                });
                                this.reload();
                            }else {
                                this.phoneyzm==''
                                this.phoneyzmdata.num--
                                this.$toast({
                                    message:""+this.phoneyzmdata.num+"",
                                    type:'fail',
                                    duration:3000,
                                });
                            }
                        }
                    },function(res){
                        console.log(res);
                    });
                }
            },
            getphone:function () {
                this.$http.post('https://www.trillionricher.com/1.0/order_bank_alipay',{
                    merber_id: this.OS.merber_id,
                    token:this.OS.token,
                },{emulateJSON:true}).then(function(res){
                    if(res.body.success==-1){
                        this.Logout()
                    } else if(res.body.success==1){
                        this.merber_phone=res.body.data.merber_phone
                    }
                },function(res){
                    console.log(res);
                });
            }

        },
        computed: {},
        watch: {},
        mounted: function () {
            this.getphone()
        }
    }
</script>

